* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  background: #f9f2eb;
}
body,
.Header,
.Footer {
  font-family: "Quicksand", sans-serif;
  background: #ffffff;
}

a {
  text-decoration: none;
}

.App {
  position: relative;
  height: calc(100vh - 1.1em);
  margin-left: auto;
  margin-right: auto;
  background: #f9f2eb;
  max-width: 720px;
}

.Header {
  position: relative;
  background: #cf995f;
  height: 3em;
  margin-bottom: 1em;
}

.Header h1 {
  color: white;
  font-size: 1.2em;
  padding-top: 15px;
  text-align: center;
}

.Footer {
  position: relative;
  bottom: 0;
  height: 3em;
  margin-top: 0.2em;
  width: 100%;
  background: #cf995f;
  color: white;
  text-align: center;
}

.HomeContent {
  display: flex;
  flex-direction: column;
  min-height: calc(100% - 6.1em);
}

.HomeContent .Links {
  margin-top: 0.2rem;
  margin-left: auto;
  margin-right: auto;
  width: calc(100% - 50px);
}

.HomeContent .Links .LinkButton {
  color: white;
  font-size: 1.1em;
  background: #d9ae7f;
  border-top: 1px solid black;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  border-top-right-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  width: calc(100% - 50px);
  height: 3rem;
  float: right;
  padding-left: 20px;
  padding-top: 0.8rem;
}

.LinkButtonShortest {
  color: white;
  font-size: 1.1em;
  background: #d9ae7f;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  border-left: 1px solid black;
  width: calc(100% - 150px);
  height: 3rem;
  float: left;
  padding-left: 20px;
  padding-top: 0.8rem;
}

.LinkButtonShort {
  color: white;
  font-size: 1.1em;
  background: #d9ae7f;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  width: calc(100% - 100px);
  height: 3rem;
  float: left;
  padding-left: 20px;
  padding-top: 0.8rem;
}

.HomeContent .Links .LinkButton:hover {
  cursor: default;
}

.HomeContent .Links .LinkButtonShort:hover {
  cursor: default;
}

.HomeContent .Links .LinkButtonShortest:hover {
  cursor: default;
}

.HomeContent .Links .HomeDeleteButton {
  font-family: "Quicksand", sans-serif;
  color: white;
  float: left;
  background: #b52b2b;
  border: none;
  border-top-left-radius: 0.5em;
  border-bottom-left-radius: 0.5em;
  border-left: 1px solid black;
  height: 3rem;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  width: 50px;
}

.HomeContent .Links .Archive {
  font-family: "Quicksand", sans-serif;
  color: white;
  float: left;
  background: #635fcf;
  border: none;
  height: 3rem;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  width: 50px;
}

.HomeContent .Links .HomeDeleteButton:hover {
  background: yellow;
}

.Input {
  position: relative;
  padding-left: 5.2em;
  margin-bottom: 1em;
}

.AddInput {
  font-family: "Quicksand", sans-serif;
  width: calc(100% - 5.2em);
  position: relative;
  height: 3em;
  color: white;
  font-size: 1em;
  border: 5px solid #cf995f;
  background: #d9ae7f;
  text-align: center;
}

.AddInputSubmit {
  width: 5.2em;
  height: 3.6em;
  position: relative;
  float: right;
  color: white;
  background: #cf7d5f;
  background: green;
  border: none;
  text-align: center;
  margin-right: 1em;
}

.Key {
  margin-top: 0.2rem;
  color: white;
  font-size: 1.1em;
  background: #d9ae7f;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-left: 1px solid black;
  border-right: 1px solid black;
  border-top-left-radius: 0.5em;
  border-bottom-left-radius: 0.5em;
  width: calc(100% - 5em);
  height: 3rem;
  float: left;
  padding-left: 1rem;
  padding-top: 0.8rem;
  margin-left: 1em;
}

.WeeksLabelActive {
  text-align: center;
  margin-top: 0.2rem;
  margin-bottom: 1rem;
  color: rgba(80, 80, 80, 0.5);
  font-size: 1.4em;
  width: calc(100%);
  height: 2rem;
  float: left;
  padding-left: 20px;
  padding-top: 0.4rem;
}

.WeeksLabelOld {
  text-align: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
  color: rgba(80, 80, 80, 0.5);
  font-size: 1.4em;
  width: calc(100%);
  height: 2rem;
  float: left;
  padding-left: 20px;
  padding-top: 0.4rem;
}

.WeekTotalValue {
  float: right;
  color: white;
  font-size: 1.1em;
  background: #cf7d5f;
  border: none;
  border-top-right-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  border-right: 1px solid black;
  height: 3rem;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  width: 50px;
  padding-left: 0.3rem;
  padding-top: 0.8rem;
}

.TotalValue {
  float: right;
  color: white;
  font-size: 1.1em;
  background: #cf7d5f;
  border: none;
  margin-top: 1rem;
  height: 3rem;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  width: 50px;
  padding-left: 0.3rem;
  padding-top: 0.8rem;
}

.TotalWeekLabel {
  margin-top: 1rem;
  color: white;
  font-size: 1.1em;
  background: #d9ae7f;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  width: calc(100% - 50px);
  height: 3rem;
  float: left;
  padding-left: 1rem;
  padding-top: 0.8rem;
}

.BackButton {
  border: none;
  background: #cf7d5f;
  color: white;
  float: left;
  width: 6em;
  height: 3em;
  font-size: 1em;
}

.MailButton {
  border: none;
  background: #cf7d5f;
  color: white;
  float: right;
  width: 6em;
  height: 3em;
  font-size: 1em;
}

.FooterHomePageText {
  font-size: 2em;
}

.HourInput {
  font-family: "Quicksand", sans-serif;
  float: left;
  color: white;
  font-size: 1.1em;
  background: #cf7d5f;
  border: none;
  margin-top: 0.2rem;
  height: 3rem;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  border-right: 1px solid black;
  border-top-right-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
  width: 50px;
  padding-left: 0.5rem;
  padding-top: 0.2rem;

  -webkit-appearance: none;
  -moz-appearance: textfield;
}

.DeleteConfirmationContainer {
  position: absolute;
  width: 100%;
  height: calc(100% - 5.8em);
  background: rgba(100, 100, 100, 0.8);
}

.DeleteConfirmation {
  position: relative;
  background: #d9ae7f;
  border-radius: 25px;
  border: 1px solid black;
  margin-top: 10rem;
  margin-left: auto;
  margin-right: auto;
  width: calc(100% - 2em);
  height: 10rem;
}

.DeleteConfirmationText {
  font-size: 1.5em;
  color: white;
  padding-top: 3rem;
  width: 14em;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.DeleteConfirmationButtons {
  position: relative;
  margin-top: 2rem;
  width: 14rem;
  margin-left: auto;
  margin-right: auto;
}

.DeleteConfirmationButtons button {
  position: relative;
  width: calc(12rem / 2);
  height: 2.5em;
  margin-right: 0.5rem;
  margin-left: 0.5rem;
}

.DeleteConfirmationYes {
  border: 1px solid black;
  background: #9e000b;
  border-radius: 0.5em;
  color: white;
}

.DeleteConfirmationNo {
  border: 1px solid black;
  border-radius: 0.5em;
  background: #cf995f;
  color: white;
}
